.methodology{
    color: white;
    display: flex;
    flex-direction: column;
    font-size: small;
    margin: 7%;
}
.upSchema{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 3%;
}
.upSchema article{
    width: 40%;
    padding: 3%;
    border-radius: 12px;
    background-color: var(--section-metodologia-bg-color);
}

.downSchema{
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.downSchema article{
    width: 40%;
    padding: 3%;
    border-radius: 12px;
    background-color: var(--section-metodologia-bg-color);
}


.lineWithCircle{
    width: 50%;
    color: black;
    display: flex;
    align-self: center;
    justify-content: space-between;
}
.line{
    width: 50%;
    height: 20%;
    color:black;
}

.circle {
    display: flex;
    height: 20px;
    width: 20px;
    border: 2px solid black;
    border-radius: 50%;
    margin-bottom: 5%;
}

@media only screen and (max-width: 600px) {
    .upSchema{
        flex-direction: column;
        justify-content: center;
    }
    .upSchema article, .downSchema article{
        width: 100%;
        margin-bottom: 5%;
    }
}